@import '../../variables.scss';

.placeholder {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;

  @media screen and (max-width: 1279px) {
    margin-bottom: 0;
  }

  &__img {
    width: 160px;
    height: 160px;
  }

  &__textwrapper {
    height: 48px;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-top: 4px;

    @media screen and (max-width: 1279px) {
      margin-top: 30px;
    }
  }

  &__text {
    font-family: Ubuntu;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: $secondary;
  }
}
